	module.exports = {"data":{"markdownRemark":{"excerpt":"React offers a great development experience but suffers from poor performance.\nThis blog post introduces Preact, a React alternative with superior performance\nbut reads and feels like React. Since it was released in March 2013, React has been taking…","html":"<p>React offers a great development experience but suffers from poor performance.\nThis blog post introduces Preact, a React alternative with superior performance\nbut reads and feels like React.</p>\n<p>Since it was released in March 2013, React has been taking over front-end\ndevelopment by storm. Unlike most popular front-end that are template based,\nReact offers a component based approach to front-end development. While React\noften requires a much larger boilerplate compared to other front-end frameworks,\nits continues to attract a large following as front-end developers find its\ndeclarative components easy to reason about. Coupled with its functional coding\nstyle which minimizes side-effects, React allows you to manage large front-end\nprojects with ease.</p>\n<p>The cost of using React, however, is its poor performance and bloated codebase\nfor its relatively small set of offerings.</p>\n<p><img src=\"https://cdn-images-1.medium.com/max/1600/1*5ojdO-TAEqnRE2OQyVHpSw.png\" alt=\"Frontend Library Benchmark\"></p>\n<p><a href=\"https://medium.com/@rajaraodv/using-preact-instead-of-react-70f40f53107c\">Source: Medium\n@rajaraodv</a></p>\n<h5><u>Poor Performance</h5>\n</u>\n<p>The figure above benchmarks a variety of popular front-end libraries/frameworks\nbased on its todo app performance. Despite employing a virtual DOM which is\nassociated with better performance, React ranks extremely low on the benchmark.</p>\n<h5><u>Bloated Codebase</h5>\n</u>\n<p>As it stands today, React's library is ~45kb large. This makes React unsuitable\nfor mobile web apps where data usage and internet speed are of concern. In fact,\n<a href=\"https://eng.uber.com/m-uber/\">Uber recently switched away from using react on their mobile web apps in favour\nof using preact</a> due to React's poor performance\non 2G networks.</p>\n<h5><u>Preact</h5>\n</u>\n<p>This brings us to the crux of this blog post - <strong>Preact</strong>.</p>\n<p>What is\n<a href=\"https://blog.logrocket.com/introduction-to-preact-a-smaller-faster-react-alternative-ad5532eb6d79\">Preact</a>?</p>\n<ul>\n<li>Preact is a lightweight alternative to React with the same ES6 API.</li>\n<li>Preact weighs in at 3kb compared to React's much larger codebase.</li>\n<li>Preact is ranked 2nd on the todo app benchmark.</li>\n</ul>\n<blockquote>\n<p><strong>But most importantly,</strong> Preact's code reads and feels like React. It offers\nthe same declarative, functional programming style that React offers and\nsupports jsx, states and React's component lifecycle methods.</p>\n</blockquote>\n<p><a href=\"https://preactjs.com/guide/getting-started\"><strong>Get started with Preact now</strong></a></p>\n<p><a href=\"https://github.com/developit/preact\">Github Repo: 13k stars</a></p>","frontmatter":{"date":"August 29, 2017","path":"noReact","title":"You Might Not Need React"}}},"pathContext":{"id":"noReact"}}

